/*ways we store objects*/
.container {
  position: relative; 
  overflow: hidden; 
}
.overlay{
  position: fixed; /* Fixes the overlay in place */
  top: 0; /* Align to the top */
  left: 50%; /* Align to the left */
  right: 50%;
  width: 100%; /* Full width */
  height: 7vw; /* Not Full height so overlay doesnt cover other elements with hover aspects */
  margin-top:0px;
  color: white; /* Text color */
  display: flex; /* Center content */
  flex-direction: row;
  overflow: hidden;
  transform: translate(-50%, 0%);
  justify-content: center; /* Center horizontally */
  z-index: 1000; /* Ensure it’s above other content */
  background-color: var(--DarkBlue);
  outline: 2px solid red;
  /*this rounds the edges at the bottom*/
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  margin-left:0px;
  display: inline-block;
  flex: 1 1 auto
}
/*style for the background image*/
.background-image {
  position: absolute; /* Positioning the image absolutely */
  left: 0; /* Align to the left */
  width: 100%; /* Make the image fill the container width */
  height: 100%; /* Maintain aspect ratio */
  z-index: 1; /* Send the image behind other content */
}
/*style for the content that is in front of the image*/
.content {
  position: relative; /* Establish positioning context for content */
  z-index: 2; /* Make sure content appears above the image */
  /* Add padding or margins as needed */
  padding: 1px;
}
/* Style the buttons */
/* Style for the button links */
.buttons a {
  color: rgb(255, 255, 255);
  text-align: center;
  text-decoration: none;
  font-size: 4vw; /* You can adjust the font size to suit your needs */
  line-height: 100%;
  padding: 1vw; /* Add padding for button size */
  border-radius: 5px; /* Rounded corners */
  height: 7vw;
  transition: background-color 0.3s ease; /* Smooth transition */
  flex: 1 1 120px; /* Allow buttons to grow and shrink, with a base size of 120px */
  min-width: 0px; /* Minimum width to prevent buttons from becoming too small */
  max-width: 200px; /* Maximum width to prevent buttons from becoming too large */
  box-sizing: border-box; /* Ensure padding is included in width calculation */
  display: inline-block; /* Make the link act like an inline block */
  margin-left:0px;
}

/* Change the background color on mouse-over */
.buttons a:hover {
  background-color: #009fa7;
  color: black;
}

/* Style the active/current link */
.buttons a.active {
  background-color: rgb(1, 61, 81);
  color: white;
}

/* Make the buttons container */
.buttons {
  display: flex; /* Flexbox layout for buttons */
  justify-content: center; /* Center the buttons horizontally */
  width: 100%; /* Take up the full width of the overlay */
  padding: 0 10px; /* Prevent buttons from touching the edges */
  flex-wrap: nowrap; /* Prevent the buttons from wrapping to the next line */
  font-size: 4vw;
  margin-left:0px;
}


